function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: 1,
        effect: "fade",
        navigation: {
            nextEl: ".main .swiper-button-next",
            prevEl: ".main .swiper-button-prev",
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderClassification() {
    var swiper = new Swiper('.classification .swiper', {
        slidesPerView: 1,
        pagination: {
            el: ".classification .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderProduction() {
    var swiper = new Swiper('.production .swiper', {
        slidesPerView: 1,
        effect: "fade",
        navigation: {
            nextEl: ".production .swiper-button-next",
            prevEl: ".production .swiper-button-prev",
        },
        pagination: {
            el: ".production .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderProduction() {
    var swiper = new Swiper('.certificates .swiper', {
        slidesPerView: 4,
        spaceBetween: 35,
        navigation: {
            nextEl: ".certificates .swiper-button-next",
            prevEl: ".certificates .swiper-button-prev",
        },
        pagination: {
            el: ".certificates .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            1451: {
                slidesPerView: 4,
                spaceBetween: 35,
            },
            966: {
                slidesPerView: 4,
                spaceBetween: 25,
            },
            741: {
                slidesPerView: 3,
                spaceBetween: 25,
            },
            481: {
                slidesPerView: 2,
                spaceBetween: 25,
            },
            320: {
                slidesPerView: 1,
                spaceBetween: 25,
            },
        }
    })
}

