function sliderWorks() {
    var swiper = new Swiper('.works .swiper', {
        slidesPerView: 2,
        spaceBetween: 35,
        navigation: {
            nextEl: ".works .swiper-button-next",
            prevEl: ".works .swiper-button-prev",
        },
        pagination: {
            el: ".works .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            1451: {
                slidesPerView: 2,
                spaceBetween: 35,
            },
            966: {
                slidesPerView: 2,
                spaceBetween: 30,
            },
            320: {
                slidesPerView: 1,
                spaceBetween: 30,
            },
        }
    })
}

$(document).ready(function() {

    sliderMain();
    sliderClassification();
    sliderProduction();
    sliderReviews();
    sliderWorks();



    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-bottom__list').toggleClass('header-bottom__list--active')
        $('body').toggleClass('fixed-body')
    })

})